import { customLabelMap } from "./custom_labels.js";

Office.onReady(() => {
  scanLabels();
  document.getElementById("scanLabelsButton").onclick = async () => {
    clearErrors();
    await scanLabels();
  };
  document.getElementById("labelForm").onsubmit = handleFormSubmit;
  document.getElementById("newSlideForm").onsubmit = handleNewSlideSubmit;
  document.getElementById("toggleSlideForm").onsubmit = handleToggleSlideSubmit;
});

async function findPattern(pattern) {
  let foundLabels = new Map();
  await PowerPoint.run(async (context) => {
    const presentation = context.presentation;
    const slides = presentation.slides;
    slides.load("items");
    await context.sync();

    for (let slide of slides.items) {
      let shapes = slide.shapes;
      shapes.load("items");
      await context.sync();

      for (let shape of shapes.items) {
        try {
          shape.textFrame.load("textRange/text");
          await context.sync();

          const text = shape.textFrame.textRange.text;
          const matches = [...text.matchAll(pattern)];
          if (matches.length > 0) {
            console.log("Match: ", matches);
          }

          for (const match of matches) {
            const customLabelName = match[1];
            const imageLabelName = match[2];
            const labelName = match[3];
            const dependsOn = match[4];
            const dependentValues = match[5];
            const options = match[6];

            if (customLabelName) {
              const key = `#${customLabelName}`;
              if (!foundLabels.has(key)) {
                foundLabels.set(key, { type: "custom" });
              }
            }
            if (imageLabelName) {
              const key = `*${imageLabelName}`;
              if (!foundLabels.has(key)) {
                foundLabels.set(key, { type: "image" });
              }
            }
            if (labelName) {
              const key = labelName;
              if (dependsOn && dependentValues) {
                const values = dependentValues.split(",").map((v) => v.trim());
                if (!foundLabels.has(key)) {
                  foundLabels.set(key, {
                    options: null,
                    dependsOn,
                    map: values,
                  });
                }
              } else if (options) {
                const opts = options.split(",").map((v) => v.trim());
                if (!foundLabels.has(key)) {
                  foundLabels.set(key, {
                    options: opts,
                    dependsOn: null,
                    map: null,
                  });
                }
              } else {
                if (!foundLabels.has(key)) {
                  foundLabels.set(key, {
                    options: null,
                    dependsOn: null,
                    map: null,
                  });
                }
              }
            }
          }
        } catch (error) {}
      }
    }
  });

  return foundLabels;
}

let validatedLabels = new Map();
async function scanLabels() {
  validatedLabels.clear();
  const pattern = /<<#(\w+)>>|<<\*(\w+)>>|<<(\w+)(?::(\w+)\[([^\]]+)\]|(?:\[([^\]]+)\]))?>>/g;
  let foundLabels = await findPattern(pattern);
  console.log("Found labels: ", foundLabels);

  validatedLabels = checkLabels(foundLabels);
  console.log("Validated", validatedLabels);

  await createToggleSlideForm();
  createInputFields(validatedLabels);
}

function checkLabels(foundLabels) {
  for (const [name, label] of foundLabels) {
    if (name.startsWith("#") && !customLabelMap.hasOwnProperty(name.substring(1))) {
      showError(`<<#${name.substring(1)}>> no existeix!`);
      continue;
    }
    if (label.dependsOn) {
      if (!foundLabels.has(label.dependsOn)) {
        showError(`<<${label.dependsOn}>> no existeix!`);
        continue;
      }
      if (foundLabels.get(label.dependsOn)["options"].length !== label.map.length) {
        showError(`El nombe d'opcions de <<${name}>> i <<${label.dependsOn}>> ha de ser igual!`);
        continue;
      }
      if (!validatedLabels.has(name)) {
        validatedLabels.set(name, label);
      }
    } else if (label.options) {
      validatedLabels.set(name, label);
    } else {
      validatedLabels.set(name, label);
    }
  }
  return validatedLabels;
}

async function handleFormSubmit(event) {
  event.preventDefault();

  const labelMap = {};

  // Helper to convert file to base64
  function fileToBase64(file) {
    return new Promise((resolve, reject) => {
      const reader = new FileReader();
      reader.onload = () => resolve(reader.result.split(",")[1]); // Remove data:image/...;base64,
      reader.onerror = reject;
      reader.readAsDataURL(file);
    });
  }

  // Collect all file input promises
  const filePromises = [];

  validatedLabels.forEach((label, name) => {
    if (label.dependsOn && label.map) {
      const sourceInput = document.getElementById(label.dependsOn);
      if (sourceInput) {
        const selectedIndex = sourceInput.selectedIndex;
        labelMap[name] = label.map[selectedIndex];
      }
    } else if (name.startsWith("*")) {
      const fileInput = document.getElementById(name);
      if (fileInput && fileInput.files.length > 0) {
        // Push a promise to convert file to base64
        filePromises.push(
          fileToBase64(fileInput.files[0]).then((base64) => {
            labelMap[name] = base64;
          })
        );
      }
    } else {
      const input = document.getElementById(name);
      if (input) {
        labelMap[name] = input.value;
      }
    }
  });

  // Wait for all file conversions
  await Promise.all(filePromises);

  console.log("Final labelMap:", labelMap);
  await replaceLabels(labelMap);
}

let originalTexts = new Map(); // Store original text for undo

async function replaceLabels(labelMap) {
  originalTexts.clear(); // Clear previous state
  await PowerPoint.run(async (context) => {
    const slides = context.presentation.slides;
    slides.load("items");
    await context.sync();

    const pattern = /<<#(\w+)>>|<<\*(\w+)>>|<<(\w+)(?::(\w+)\[([^\]]+)\]|(?:\[([^\]]+)\]))?>>/g;
    for (let slideIdx = 0; slideIdx < slides.items.length; slideIdx++) {
      const slide = slides.items[slideIdx];
      const shapes = slide.shapes;
      shapes.load("items");
      await context.sync();

      for (let shapeIdx = 0; shapeIdx < shapes.items.length; shapeIdx++) {
        try {
          const shape = shapes.items[shapeIdx];
          shape.textFrame.load("textRange/text");
          await context.sync();

          const textRange = shape.textFrame.textRange;
          const text = textRange.text;

          const matches = [...text.matchAll(pattern)];

          const replacements = [];
          for (let i = matches.length - 1; i >= 0; i--) {
            const match = matches[i];
            const fullMatch = match[0];
            const customLabelName = match[1]; // <<#name>>
            const imageLabelName = match[2]; // <<*name>>
            const labelName = match[3]; // <<labelName...>>
            const dependsOn = match[4];
            const dependentValues = match[5];
            const options = match[6];

            let replacementValue = null;

            if (customLabelName && customLabelMap.hasOwnProperty(customLabelName)) {
              replacementValue = customLabelMap[customLabelName]();
            } else if (imageLabelName && labelMap[`*${imageLabelName}`]) {
              // Replace the shape with an image if <<*name>> is found
              const base64 = labelMap[`*${imageLabelName}`];
              // Remove the label text
              const subRange = textRange.getSubstring(match.index, fullMatch.length);
              subRange.text = "";
              // Set the image as fill (if possible)
              await shape.fill.setImage(base64);
              // Store the replacement for undo (removes label text, not image)
              replacements.push({
                index: match.index,
                original: fullMatch,
                replaced: "",
              });
              continue; // Skip further text replacement for this match
            } else if (dependsOn && dependentValues) {
              const sourceValue = labelMap[dependsOn];
              const values = dependentValues.split(",").map((v) => v.trim());
              const sourceOptions = validatedLabels.get(dependsOn)?.options || [];
              const index = sourceOptions.indexOf(sourceValue);
              if (index !== -1 && index < values.length) {
                replacementValue = values[index];
              }
            } else if (labelName) {
              replacementValue = labelMap[labelName];
            }

            if (replacementValue !== null && replacementValue !== undefined) {
              const subRange = textRange.getSubstring(match.index, fullMatch.length);
              subRange.text = replacementValue;
              replacements.push({
                index: match.index,
                original: fullMatch,
                replaced: replacementValue,
              });
            }
          }
          originalTexts.set(`${slideIdx}_${shapeIdx}`, replacements);
        } catch (error) {}
      }
    }
    console.log("originals: ", originalTexts);
    addUndoButton();
    await context.sync();
  });
  const substituirBtn = document.getElementById("substituirBtn");
  if (substituirBtn) {
    substituirBtn.remove();
  }
}

async function undoReplaceLabels() {
  await PowerPoint.run(async (context) => {
    const slides = context.presentation.slides;
    slides.load("items");
    await context.sync();

    for (let slideIdx = 0; slideIdx < slides.items.length; slideIdx++) {
      const slide = slides.items[slideIdx];
      const shapes = slide.shapes;
      shapes.load("items");
      await context.sync();

      for (let shapeIdx = 0; shapeIdx < shapes.items.length; shapeIdx++) {
        try {
          const shape = shapes.items[shapeIdx];
          shape.textFrame.load("textRange/text");
          await context.sync();

          const textRange = shape.textFrame.textRange;
          const key = `${slideIdx}_${shapeIdx}`;
          const replacements = originalTexts.get(key);

          if (replacements && replacements.length > 0) {
            console.log("Replacemtns: ", replacements);

            for (let i = replacements.length - 1; i >= 0; i--) {
              const { index, original, replaced } = replacements[i];
              const subRange = textRange.getSubstring(index, replaced.length);
              subRange.load("text");
              await context.sync();
              if (subRange.text === replaced) {
                subRange.text = original;
              } else {
                showError("Com que s'ha modificat el text no s'han pogut desfer alguns canvis.");
              }
            }
          }
        } catch {}
      }
    }
    await context.sync();
  });
  originalTexts.clear();
  scanLabels();
}

function addUndoButton() {
  const container = document.getElementById("inputContainer");
  let undoBtn = document.getElementById("undoButton");
  if (!undoBtn) {
    undoBtn = document.createElement("button");
    undoBtn.id = "undoButton";
    undoBtn.type = "button";
    undoBtn.className = "inputButton";
    undoBtn.textContent = "↩️ DESFER";
    undoBtn.onclick = async () => {
      await undoReplaceLabels();
    };
    container.appendChild(undoBtn);
  }
  if (originalTexts.size <= 0) {
    undoBtn.remove();
    return;
  }
}

function createInputFields(labels) {
  const container = document.getElementById("inputContainer");
  container.innerHTML = "";

  labels.forEach((label, name) => {
    if (label.dependsOn) return;
    if (name.startsWith("#")) return; // Skip custom labels (auto-filled)

    const div = document.createElement("div");
    div.className = "form-group";

    const labelEl = document.createElement("label");
    labelEl.setAttribute("for", name);
    if (name.startsWith("*")) {
      labelEl.textContent = name.substring(1);
    } else {
      labelEl.textContent = name;
    }
    div.appendChild(labelEl);

    // Add file input for image labels (<<*name>>)
    if (name.startsWith("*")) {
      const fileInput = document.createElement("input");
      fileInput.type = "file";
      fileInput.id = name;
      fileInput.name = name;
      fileInput.accept = "image/*";
      fileInput.required = true;
      div.appendChild(fileInput);
    } else if (Array.isArray(label.options)) {
      const select = document.createElement("select");
      select.id = name;
      select.className = "selector";
      select.name = name;

      label.options.forEach((opt) => {
        const optionEl = document.createElement("option");
        optionEl.value = opt;
        optionEl.textContent = opt;
        select.appendChild(optionEl);
      });

      div.appendChild(select);
    } else {
      const input = document.createElement("input");
      input.type = "text";
      input.id = name;
      input.name = name;
      input.required = true;
      div.appendChild(input);
    }

    container.appendChild(div);
  });

  if (labels.size > 0) {
    const submit = document.createElement("button");
    submit.id = "substituirBtn";
    submit.type = "submit";
    submit.className = "inputButton";
    submit.textContent = "✅ SUBSTITUIR";
    container.appendChild(submit);
  } else {
    const msg = document.createElement("p");
    msg.textContent = "No s'ha trobat cap etiqueta.";

    container.appendChild(msg);
  }
  addUndoButton();
}

async function getAllSlideTags() {
  const tags = [];
  await PowerPoint.run(async (context) => {
    const slides = context.presentation.slides;
    slides.load("items");
    await context.sync();
    for (let i = 0; i < slides.items.length; i++) {
      const slide = slides.items[i];
      slide.tags.load("items");
      await context.sync();
      for (const tag of slide.tags.items) {
        if (tag.key === "TOGGLE") {
          tags.push({ idx: i, tag: tag.value });
          break;
        }
      }
    }
  });
  return tags;
}

async function handleNewSlideSubmit(event) {
  event.preventDefault();

  const input = document.getElementById("slideName");
  if (!input || !input.value.trim()) return;
  const tagValue = input.value.trim();

  await PowerPoint.run(async (context) => {
    const slides = context.presentation.slides;
    slides.load("items");
    await context.sync();

    const selection = context.presentation.getSelectedSlides();
    selection.load("items");
    await context.sync();

    if (selection.items.length > 0) {
      const selectedId = selection.items[0].id;
      const slideIdx = slides.items.findIndex((s) => s.id === selectedId);
      if (slideIdx !== -1) {
        const slide = slides.items[slideIdx];
        slide.tags.load("items");
        await context.sync();
        slide.tags.delete("TOGGLE");
        slide.tags.add("TOGGLE", tagValue);
        await context.sync();
      }
    }
  });

  console.log(await getAllSlideTags());
  input.value = "";
  createToggleSlideForm();
}

async function createToggleSlideForm() {
  const container = document.getElementById("toggleSlideFormContainer");
  container.style.display = "block";
  container.innerHTML = "";

  const tags = await getAllSlideTags();

  if (tags.length === 0) {
    container.textContent = "No tagged slides found.";
    return;
  }

  for (const { idx, tag } of tags) {
    const div = document.createElement("div");
    div.style.display = "flex";
    div.style.alignItems = "center";
    div.style.justifyContent = "space-between";
    div.style.marginBottom = "8px";

    const labelWrapper = document.createElement("div");
    labelWrapper.style.display = "flex";

    labelWrapper.style.gap = "6px";

    const checkbox = document.createElement("input");
    checkbox.type = "checkbox";
    checkbox.checked = true;
    checkbox.className = "checkmark";
    checkbox.id = `slideVisible_${idx}`;

    const label = document.createElement("label");
    label.htmlFor = checkbox.id;
    label.textContent = `${idx + 1}. ${tag}`;
    label.style.whiteSpace = "nowrap";

    labelWrapper.appendChild(checkbox);
    labelWrapper.appendChild(label);

    const deleteBtn = document.createElement("button");
    deleteBtn.type = "button";
    deleteBtn.style.marginTop = "12px";
    deleteBtn.textContent = "❌";
    deleteBtn.onclick = async () => {
      await PowerPoint.run(async (context) => {
        const slides = context.presentation.slides;
        slides.load("items");
        await context.sync();
        const slide = slides.items[idx];
        slide.tags.load("items");
        await context.sync();
        slide.tags.delete("TOGGLE");
      });
      console.log(await getAllSlideTags());
      await createToggleSlideForm();
    };

    div.appendChild(labelWrapper);
    div.appendChild(deleteBtn);
    container.appendChild(div);
  }

  const submit = document.createElement("button");
  submit.type = "submit";
  submit.className = "inputButton";
  submit.textContent = "✅ APLICAR";
  container.appendChild(submit);
}

async function handleToggleSlideSubmit(event) {
  event.preventDefault();

  // Get all tags (slide indices with TOGGLE tag)
  const tags = await getAllSlideTags();

  // Collect indices of slides to keep (checked)
  const slidesToKeep = [];
  tags.forEach(({ idx }) => {
    const checkbox = document.getElementById(`slideVisible_${idx}`);
    if (checkbox && checkbox.checked) {
      slidesToKeep.push(idx);
    }
  });

  // Delete slides that are not checked
  await PowerPoint.run(async (context) => {
    const slides = context.presentation.slides;
    slides.load("items");
    await context.sync();

    // Build a set for fast lookup
    const keepSet = new Set(slidesToKeep);

    // Delete from last to first to avoid index shifting
    for (let i = slides.items.length - 1; i >= 0; i--) {
      // Only delete slides that have a TOGGLE tag and are not checked
      const tagObj = tags.find((t) => t.idx === i);
      if (tagObj && !keepSet.has(i)) {
        slides.items[i].delete();
      }
    }
    await context.sync();
  });

  await scanLabels();
}

function showError(msg) {
  const container = document.getElementById("errorContainer");

  const id = String(Date.now());

  const div = document.createElement("div");
  div.className = "error-message";
  div.id = id;
  div.textContent = `⚠️ ${msg}`;

  const btn = document.createElement("button");
  btn.className = "close-btn";
  btn.onclick = () => {
    document.getElementById(id).style.display = "none";
  };
  btn.textContent = "✖";

  div.appendChild(btn);
  container.appendChild(div);
}

function clearErrors() {
  const container = document.getElementById("errorContainer");
  container.innerHTML = "";
}
