export const customLabelMap = {
  any: () => new Date().getFullYear().toString(),
  mes: () => {
    const mesosCatalans = [
      "gener",
      "febrer",
      "març",
      "abril",
      "maig",
      "juny",
      "juliol",
      "agost",
      "setembre",
      "octubre",
      "novembre",
      "desembre",
    ];
    return mesosCatalans[new Date().getMonth()];
  },
  dia: () => new Date().getDate().toString().padStart(2, "0"),
  any_passat: () => (new Date().getFullYear() - 1).toString(),
};
